$main-color: #e0c9a6;

h2 {
  font-weight: 400;
  font-size: 20px;
  margin-bottom: 20px;
}

p {
  line-height: 25px;
}
#note.note--main-style {
  scale: 1;
  margin: 0 auto;
  top: 50%;
  left: 0;
}

.content-main {
  scale: 0.2;
  position: relative;
  width: 400px;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  z-index: 1;

  .content-box {
    height: auto;
    overflow: hidden;
    padding: 20px;
    background: $main-color;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.05);

    &:before,
    &:after {
      content: "";
      height: 2px;
      position: absolute;
      left: 0;
      right: 0;
      -webkit-clip-path: polygon(
        0% 0%,
        5% 100%,
        10% 0%,
        15% 100%,
        20% 0%,
        25% 100%,
        30% 0%,
        35% 100%,
        40% 0%,
        45% 100%,
        50% 0%,
        55% 100%,
        60% 0%,
        65% 100%,
        70% 0%,
        75% 100%,
        80% 0%,
        85% 100%,
        90% 0%,
        95% 100%,
        100% 0%
      );
    }
    &:before {
      background-color: #3e3f40;
      top: 0;
    }
    &:after {
      background-color: $main-color;
      bottom: -2px;
    }
  }
}
